}

#f price {
	font-weight: 900;
	font-size: 150px;
	margin-bottom: 20px;
}

#f cross {
	text-decoration: line-through;
	font-weight: 700;
}

#f h5 {
	/*color: #b9b9b9;*/
	font-weight: 400;
	text-transform: uppercase;
}

/* COPYRIGHT */
#copyright {
	padding-bottom: 35px;
	padding-top: 35px;
	background: #1d1d1d;
	color: white;
}

#copyright h6 {
	font-size: 14px;
	letter-spacing: 0.8px;
}

.badge-new {
	background: #E91E63;
	border-radius: 100%;
