.modal-dialog {
	top: -100%;
}


.customer_info_wrapper{
	background-image: url('../images/arches.png');
	padding: 6em 0px;

}

.customer_info_tile{
    vertical-align: top;
    position: relative;
    min-height: 1px;
    padding-left: 0px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    .count {
    	font-size: 3em;
    	color: #33363e;
     }
    .tile_name{
    	margin-top: 1.5em;
    	padding-top: 1.1em;
    	position: relative;
    	padding-bottom: 0.18em;
    	text-transform: capitalize;
    	color: #232a34;
    	&:before{ 
    		content: ' ';
    		position: absolute;
		    top: 0;
		    left: 45%;
		    display: block;
		    width: 10.5%;
		    height: 3px;
		    background-color: #fac102;
    	}
    }
    .tile_container{
    	box-shadow: 1px 2px 10px #717171 !important;
    	border-radius: 5px;
    	-webkit-border-radius: 5px;
    	background-color: #f0f0f0;
		text-align: center;
    	display: inline-block;
    	width: 100%;
	    -webkit-box-sizing: border-box;
	    -moz-box-sizing: border-box;
	    box-sizing: border-box;
	    padding: 3em 1em;
	    img {
	    	height: 78px;
	    }
    }
}

