/*	################################################################
	1. GENERAL STRUCTURES
################################################################# */

/* Import fonts */

@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900);

* {
  margin: 0;
  padding: 0px;
  font-family: 'Lato', sans-serif;
}

body {
  background: #fff;
  margin: 0;
  color: #5a5a5a;
}

h1,h2,h3,h4,h5,h6 {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
}

p {
  padding: 0;
  margin-bottom: 12px;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 26px;
  /*color: #666;*/
  margin-top: 10px;
}

html,body {
  height: 100%;
}

.aligncenter {
  margin-left: auto;
  margin-right: auto;
  display: block;
  clear: both;
}

.centered {
  text-align: center;
}

.mt {
  margin-top: 50px;
}

.mb {
  margin-bottom: 50px;
}

.mtb {
  margin-top: 50px;
  margin-bottom: 50px;
}

.mtb2 {
  margin-top: 100px;
  margin-bottom: 100px;
}

.ptb {
  padding-top: 80px;
  padding-bottom: 80px;
}

::-moz-selection {
  color: #fff;
  text-shadow: none;
  background: #2B2E31;
}

::selection {
  color: #fff;
  text-shadow: none;
  background: #2B2E31;
}

*,*:after,*:before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  padding: 0;
  margin: 0;
  text-decoration: none;
  transition: background-color .4s linear, color .4s linear;
  color: #F7CA18;
}

a:hover,a:focus {
  text-decoration: none;
  color: #696E74;
}

/* BOOTSTRAP MODIFICATIONS - MODAL */

.modal-footer {
  padding: 15px;
  text-align: right;
  border-top: 1px solid #e5e5e5;
  background-color: #b9b9b9;
}

.modal-title {
  font-weight: 700;
}

.modal-header {
  background-color: #F7CA18;
}

/* MENU CONFIGURATION*/

/* Push the body after clicking the menu button */

.body-push {
  overflow-x: hidden;
  position: relative;
  left: 0;
}

.body-push {
  transition: all .3s ease;
}

/* FORM CONFIGURATION */

input {
  font-size: 16px;
  min-height: 40px;
  border-radius: 2px;
  line-height: 20px;
  padding: 11px 30px 12px;
  border: 1px solid #b9b9af;
  margin-bottom: 10px;
  background-color: #fff;
  transition: background-color 0.2s;
}

/* Buttons */

.btn-design {
  font-size: 13px;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-left: 8px;
  margin-right: 8px;
}

.btn-y {
  background: #F7CA18;
  color: #2f2f2f;
}

.btn-y:hover {
  background: transparent;
  color: white;
  border: 1px solid #F7CA18;
}

/* SLIDER */

#slides {
  position: relative;
  max-height: 1020px;
  background: #1D1D25;
  min-height: 100%;
}

#slides ul li img {
  opacity: 0.9;
}

.slides-pagination {
  margin-bottom: -40px;
}

.slides-navigation a i {
  padding: 5px;
  font-size: 30px;
  color: white;
  margin: 10px;
}

.slides-pagination a {
  display: block;
  display: inline-block;
  overflow: hidden;
  margin: 8px;
  margin-bottom: 60.5px;
  width: 20px;
  height: 20px;
  border: 4px inset #ffffff;
  border-radius: 1px;
  vertical-align: middle;
  text-indent: -100%;
  opacity: .3;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
  zoom: 1;
  zoom: 1;
}

/* HERO banner with fixed position */

#bannertext {
  position: fixed;
  top: 25%;
  right: 0;
  left: 0;
  z-index: 2;
  margin: auto;
  max-width: 1200px;
  color: #fff;
  text-align: center;
}

#bannertext h1 {
  position: relative;
  margin-bottom: 0;
  color: white;
  letter-spacing: 2px;
  font-weight: 900;
  font-size: 3.1em;
  line-height: 1em;
}

#bannertext h3 {
  position: relative;
  color: white;
  font-size: 22px;
  font-weight: 300;
  letter-spacing: 2px;
  margin-top: 20%;
}

#bannertext bold {
  position: relative;
  color: white;
  font-size: 28px;
  font-weight: 700;
  letter-spacing: 2px;
}

#bannertext p {
  margin-top: 25px;
}

#bannertext hr {
  border: 1px solid white;
  width: 150px;
}

@media (max-width: 767px) {
  #bannertext h1 {
    font-size: 60px;
  }

  #bannertext h3 {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  #bannertext h1 {
    font-size: 30px;
  }

  #bannertext h3 {
    font-size: 13px;
  }
}

/* MAIN CONFIGURATION - BASE MODIFICATION */

h1 {
  font-weight: 900;
  font-size: 38px;
}

h2 {
  font-weight: 300;
  font-size: 22px;
  /*color: #b9b9b9;*/
  text-transform: uppercase;
}

.lead {
  font-size: 22px;
}

/* SEPARATOR SECTION */

/* CIRCLE Icons */

.circle-icon {
  width: 120px;
  height: 120px;
  margin: 0 auto;
  border-radius: 50%;
  background-color: transparent;
  border: 1px solid #2f2f2f;
  position: relative;
}

.circle-icon:hover {
  background-color: #F7CA18;
}

.circle-icon .icon {
  color: #2f2f2f;
  line-height: 120px;
  text-align: center;
}

.icon {
  font-size: 50px;
}

/* CONFIGURATION for White Section */

.w h5 {
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 20px;
}

.w hr {
  border: 1px solid #2f2f2f;
  width: 40px;
}

.w img {
  width: 100%;
  height: auto;
}

/* Grey Section */

.g {
  background: #f2f2f2;
  padding-top: 40px;
}

/* FOOTER */

#f {
  padding-bottom: 40px;
  padding-top: 40px;
  margin-top: 0px;
  background: #2f2f2f;
  color: white;
}

#f .icon {
  margin-right: 25px;
  font-size: 35px;
}

#f price {
  font-weight: 900;
  font-size: 150px;
  margin-bottom: 20px;
}

#f h5 {
  /*color: #b9b9b9;*/
  font-weight: 400;
  text-transform: uppercase;
}

/* COPYRIGHT */

#copyright {
  padding-bottom: 35px;
  padding-top: 35px;
  background: #1d1d1d;
  color: white;
}

#copyright h6 {
  font-size: 14px;
  letter-spacing: 0.8px;
}

.li_1 {
  background-image: url("/static/website/assets-solar/img/sun_and_hand.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 1px -914px;
  background-size: 100%;
}

#refreashDiv {
  position: absolute;
  z-index: 100;
  width: 100%;
  background-color: white;
  padding: 10px;
  display: none;
}

#refreashDiv button {
  position: absolute;
  right: 0px;
  top: 17px;
  right: 17px;
}

.modal-dialog {
  top: -100%;
}

.customer_info_wrapper {
  background-image: url("../images/arches.png");
  padding: 6em 0px;
}

.customer_info_tile {
  vertical-align: top;
  position: relative;
  min-height: 1px;
  padding-left: 0px;
  box-sizing: border-box;
}

.customer_info_tile .count {
  font-size: 3em;
  color: #33363e;
}

.customer_info_tile .tile_name {
  margin-top: 1.5em;
  padding-top: 1.1em;
  position: relative;
  padding-bottom: 0.18em;
  text-transform: capitalize;
  color: #232a34;
}

.customer_info_tile .tile_name:before {
  content: ' ';
  position: absolute;
  top: 0;
  left: 45%;
  display: block;
  width: 10.5%;
  height: 3px;
  background-color: #fac102;
}

.customer_info_tile .tile_container {
  box-shadow: 1px 2px 10px #717171 !important;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  background-color: #f0f0f0;
  text-align: center;
  display: inline-block;
  width: 100%;
  box-sizing: border-box;
  padding: 3em 1em;
}

.customer_info_tile .tile_container img {
  height: 78px;
}

.pad-30 {
  padding-right: 30px;
}

@media (max-width: 767px) {
  .pad-30 {
    padding-right: 0px;
    padding-top: 25px;
  }
}